import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'pigs',
    loadChildren: () =>
      import('./pigs/pigs.module').then((mod) => mod.PigsModule),
  },
  {
    path: 'staff',
    loadChildren: () =>
      import('./staff/staff.module').then((mod) => mod.StaffModule),
  },
  { path: '', pathMatch: 'full', redirectTo: '/pigs' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
